<template>
  <div>
    <el-breadcrumb class="path" separator-class="el-icon-arrow-right">
      <span style="float:left">当前位置：</span>
      <el-breadcrumb-item>文章管理</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/article/tag' }">文章标签</el-breadcrumb-item>
    </el-breadcrumb>
    <el-row>
      <el-col :lg="5" :md="5" :sm="24" :xs="24" class="tag-left">
        <p>添加标签</p>
        <div class="form-box">
          <el-form :rules="rules" ref="dataForm" :model="Form" label-position="top">
            <el-form-item label="标签名称：" prop="name" :inline-message="true">
                <el-input v-model="Form.name" placeholder="标签名称"></el-input>
            </el-form-item>
            <el-form-item label="标签别名：" prop="alias" :inline-message="true">
                <el-input v-model="Form.alias" placeholder="标签别名"></el-input>
            </el-form-item>
          </el-form>
          <el-button type="primary" class="submit" icon="el-icon-check" size="medium">添加标签</el-button>
        </div>
      </el-col>
      <el-col :lg="17" :md="17" :sm="24" :xs="24" class="tag-right">
        <p>标签列表</p>
        <div class="tag-list">
          <el-table
        ref="multipleTable"
          border
          stripe
          :data="tableData"
        v-loading="loading"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
        >
          <el-table-column
            type="selection"
            width="55">
          </el-table-column>
          <el-table-column
            label="ID"
            width="55">
            <template slot-scope="scope">{{ scope.$index + 1 }}</template>
          </el-table-column>
          <el-table-column
            prop="name"
            label="标签名称"
            show-overflow-tooltip
            >
          </el-table-column>
          <el-table-column
            prop="alias"
            label="标签别名"
            show-overflow-tooltip
            >
          </el-table-column>
          <el-table-column
            prop="article_num"
            label="文章数量"
            show-overflow-tooltip
            >
          </el-table-column>
          <el-table-column
          label="操作"
          show-overflow-tooltip>
           <template slot-scope="scope">
              <el-button
                size="mini"
                type="primary"
                icon="el-icon-edit"
                circle
                v-waves
                @click="handleEdit(scope.row._id)"></el-button>
                <el-button
                size="mini"
                v-waves
                type="danger"
                icon="el-icon-delete"
                circle
                @click="handleDelete(scope.row._id)"></el-button>
            </template>
        </el-table-column>
        </el-table>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import waves from '@/directive/waves'
export default {
  directives: {
    waves
  },
  data () {
    return {
      loading: false,
      tableData: [
        {
          _id: 1,
          name: 'javascript',
          alias: 'js',
          article_num: '6'
        }
      ],
      Form: {
        name: '',
        alias: ''
      },
      rules: {
        name: [{ required: true, message: '请输入标签名称', trigger: 'blur' }],
        alias: [{ required: true, message: '请输入标签名称', trigger: 'blur' }]
      },
      multipleSelection: []
    }
  },
  methods: {
    handleSelectionChange (val) {
      this.multipleSelection = val
    }
  }
}
</script>

<style lang="stylus" scoped>
.tag-left
  margin 0 15px
  background #fff
  p
    line-height 38px
    background #eee
    color #333
    padding-left 8px
  .form-box
    padding 0 20px
    background #fff
    .el-form-item
      margin-bottom 0
      .el-form-item__label
        label.el-form-item__label
          line-height 26px
    .submit
      display block
      margin 30px auto 20px auto
      width 90%
.tag-right
  margin 0 10px
  background #fff
  p
    line-height 38px
    background #eee
    color #333
    padding-left 8px
</style>



